*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --text-color-primary: #2c2c2c;
  --text-color-accent: #bbf330;
  --text-color-placeholder: #505050;
  --text-color-link: #7eb101;
  --text-color-menu: #000;
  --error-color: #ff705d;
  --bnt-background-green: #bbf330;
  --bnt-background-green-acent: #b5ff00;
  --bnt-background-white: #fff;
  --bnt-background-yellow: #ffe550;
  --btn-border: #7eb101;
  --btn-border-acent: #597d00;
  --background-primary: #fff;
  --background-second: # #fefefe;
  --background-yellow: #ffe550;
  --background-circles: #fefefe;
  --box-shadow-header: 0px 4px 24px rgba(105, 105, 105, .1);
  --box-shadow-footer: 0px -4px 24px rgba(105, 105, 105, .1);
  --swiper-navigation-size: 40px;
  --padding-v-desk: 16px;
  --padding-v-tab: 32px;
  --padding-v-mob: 20px;
  --duration-primary: 250ms;
  --timing-function-primary: cubic-bezier(.4, 0, .2, 1);
}

body {
  color: var(--text-color-primary);
  font-family: roboto, sans-serif;
}

