}

.menu {
  opacity: 0;
  z-index: 2;
  background-color: var(--background-second);
  border-radius: 30px 0 30px 30px;
  transition: all .3s;
  position: absolute;
  top: 50px;
  right: 20px;
  overflow: hidden;
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

